import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

// const CORS_PROXY = 'https://cors-anywhere.herokuapp.com/'
// // const CORS_PROXY = 'https://crossorigin.me/'
// const CORS_PROXY = 'https://api.allorigins.win/raw?url='
// // const CORS_PROXY = 'https://yacdn.org/proxy/'
const CORS_PROXY = [
  'https://test.cors.workers.dev/?',
  'https://api.allorigins.win/raw?url=',
  'https://cors-anywhere.herokuapp.com/',
]
import P from 'rss-parser'
const paser = new P({
  customFields: {
    item: ['description'],
  },
  headers: { Origin: '*' },
})
export default new Vuex.Store({
  state: {
    news: {},
    loading: true,
    snackbar: false,
    error: '',
    url: localStorage.getItem('last'),
    onlyTitle: JSON.parse(localStorage.getItem('onlytitle')),
    dark: JSON.parse(localStorage.getItem('dark')),
    route: JSON.parse(localStorage.getItem('route')) || 0,
  },
  mutations: {
    update(state, data) {
      state.news = data
    },
    hideLoading(state) {
      state.loading = false
    },
    showLoading(state) {
      state.loading = true
    },
    showSnackbar(state, txt) {
      state.snackbar = true
      state.error = txt
    },
    saveUrl(state, url) {
      state.url = url
      localStorage.setItem('last', url)
    },
    switchTitle(state) {
      state.onlyTitle = !state.onlyTitle
      localStorage.setItem('onlytitle', state.onlyTitle)
    },
    setTheme(state, value) {
      state.dark = value
      localStorage.setItem('dark', value)
    },
    switchRoute(state, route) {
      state.route = route
      localStorage.setItem('route', route)
    },
  },
  actions: {
    async loadNews({ commit, state }, url) {
      const URL = url || state.url
      commit('saveUrl', URL)
      commit('showLoading')
      try {
        const data = await paser.parseURL(CORS_PROXY[state.route] + URL)
        commit('update', data)
      } catch (error) {
        commit('showSnackbar', error)
      }
      commit('hideLoading')
    },
  },
  modules: {},
})
