<template>
  <main>
    <v-progress-linear v-if="$store.state.loading" fixed indeterminate :color="$store.state.dark ? '#666' : 'primary'"></v-progress-linear>
    <v-container>
      <v-list three-line :dark="$store.state.dark">
        <v-subheader
          :key="$store.state.news.title"
          v-text="$store.state.news.title"
        ></v-subheader>
        <template v-for="(item, index) in $store.state.news.items">
          <v-list-item
            :key="index+.1"
            @click="handleView(item)"
          >
            <v-list-item-content>
              <v-list-item-title>
                <span v-text="item.title"></span>
              </v-list-item-title>
              <v-list-item-subtitle v-if="item.description" v-show="!$store.state.onlyTitle">
                <div v-html="item.description"></div>
              </v-list-item-subtitle>
              <v-list-item-subtitle v-else v-show="!$store.state.onlyTitle">
                <div v-if="item.title.trim() !== item.contentSnippet.trim()" v-html="item.contentSnippet.trim()"></div>
              </v-list-item-subtitle>
            </v-list-item-content>
            <v-list-item-icon v-if="item.title.trim() === item.content.trim()">
              <v-icon>mdi-open-in-new</v-icon>
            </v-list-item-icon>
          </v-list-item>
          <v-divider
            :key="index"
            :inset="item.inset"
          ></v-divider>
        </template>
      </v-list>
    </v-container>
  </main>
</template>

<script>
export default {
  setup(_, ctx) {
    const handleView = item => {
      if (item.title.trim() === item.content.trim()) window.open(item.link)
      else {
        ctx.root.$router.push({ name: 'item', params: item })
        ctx.root.$vuetify.goTo(0)
      }
    }
    return {
      handleView,
    }
  },
}
</script>