<template>
  <v-navigation-drawer
    v-model="show"
    app
    temporary
    :dark="$store.state.dark"
  >
    <v-subheader>设置</v-subheader>
    <v-list-item @click="showDefaultFeeds=!showDefaultFeeds">
      <v-list-item-content>
        <v-list-item-title>显示默认列表</v-list-item-title>
      </v-list-item-content>
      <v-list-item-action>
        <v-checkbox :color="$store.state.dark ? 'black' : 'primary'" @click.native.stop v-model="showDefaultFeeds"></v-checkbox>
      </v-list-item-action>
    </v-list-item>
    <v-list-item @click="darkMode=!darkMode">
      <v-list-item-content>
        <v-list-item-title>夜间模式</v-list-item-title>
      </v-list-item-content>
      <v-list-item-action>
        <v-checkbox :color="$store.state.dark ? 'black' : 'primary'" @click.native.stop v-model="darkMode"></v-checkbox>
      </v-list-item-action>
    </v-list-item>
    <v-subheader>订阅列表</v-subheader>
    <v-list-item
      v-for="(li, i) of currFeeds"
      :key="i"
      @click="show=false;$store.dispatch('loadNews', li.feedUrl)"
    >
      <v-list-item-content>
        <v-list-item-title v-text="li.title"></v-list-item-title>
      </v-list-item-content>
      <v-list-item-action v-if="!li.default" @click.stop="handleDelete(li.feedUrl)">
        <v-icon>mdi-delete-outline</v-icon>
      </v-list-item-action>
    </v-list-item>
    <v-subheader>工具</v-subheader>
    <v-list-item @click="importFile">
      <v-list-item-icon>
        <v-icon>mdi-file-import</v-icon>
      </v-list-item-icon>
      <v-list-item-content>
        <v-list-item-title>导入</v-list-item-title>
      </v-list-item-content>
    </v-list-item>
    <v-list-item @click="exportFile">
      <v-list-item-icon>
        <v-icon>mdi-file-export</v-icon>
      </v-list-item-icon>
      <v-list-item-content>
        <v-list-item-title>导出</v-list-item-title>
      </v-list-item-content>
    </v-list-item>
    <v-list-item @click="visibleAddDialog=true">
      <v-list-item-icon>
        <v-icon>mdi-plus</v-icon>
      </v-list-item-icon>
      <v-list-item-content>
        <v-list-item-title>添加</v-list-item-title>
      </v-list-item-content>
    </v-list-item>
    <v-list-item @click="0" @touchstart="godMode">
      <v-list-item-icon>
        <v-icon>mdi-information-outline</v-icon>
      </v-list-item-icon>
      <v-list-item-content>
        <v-list-item-title>关于</v-list-item-title>
        <v-list-item-subtitle>1.3.0</v-list-item-subtitle>
      </v-list-item-content>
    </v-list-item>

    <input style="display:none" @change="selectedFile" type="file" ref="file" accept="text/xml" />

    <v-dialog v-model="visibleAddDialog" persistent max-width="600px">
      <v-card>
        <v-card-title>
          <span class="headline">添加订阅</span>
        </v-card-title>
        <v-card-text>
          <v-container>
            <v-row>
              <v-col cols="12">
                <v-text-field label="Title*" v-model="addObj.title" required></v-text-field>
              </v-col>
              <v-col cols="12">
                <v-text-field label="URL*" type="url" v-model="addObj.feedUrl" required></v-text-field>
              </v-col>
            </v-row>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="primary darken-4" text @click="visibleAddDialog = false">取消</v-btn>
          <v-btn color="primary darken-4" text @click="handleAdd">添加</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-navigation-drawer>
</template>

<script>
import parseOpml from 'node-opml-parser'
import opml from 'opml-generator'
import defaultFeeds from '../feeds'
import { ref, watch, computed, reactive } from '@vue/composition-api'
import { cl } from '../feeds'
export default {
  model: {
    event: 'toggle',
    prop: 'visible',
  },
  props: {
    visible: {
      type: Boolean,
      default: false,
    },
  },
  setup(props, ctx) {
    const show = ref(false)
    const darkMode = ref(ctx.root.$store.state.dark)
    watch(() => ctx.root.$store.commit('setTheme', darkMode.value))
    watch(() => props.visible, v => show.value = v)
    watch(show, v => ctx.emit('toggle', v))
    const localFeeds = localStorage.getItem('feed') ? JSON.parse(localStorage.getItem('feed')) : []
    const userFeeds = ref(localFeeds)
    watch(() => localStorage.setItem('feed', JSON.stringify(userFeeds.value)))
    watch(() => localStorage.setItem('settting', showDefaultFeeds.value))
    const mode = ref(0)
    const currFeeds = computed(() => {
      if (mode.value) return cl
      else {
        const mergerDefault = defaultFeeds.filter(feed => !userFeeds.value.find(my => my.feedUrl === feed.feedUrl))
        return userFeeds.value.length !== 0 ? showDefaultFeeds.value ? [...userFeeds.value, ...mergerDefault] : userFeeds.value : defaultFeeds
      }
    })
    watch(mode, () => ctx.root.$store.dispatch('loadNews', currFeeds.value[0].feedUrl), { lazy: true })

    const settting = localStorage.getItem('settting') || 'true'
    const showDefaultFeeds = ref(settting === 'true')
    const importFile = () => {
      if (mode.value === 1) return mode.value = 0
      ctx.refs.file.click()
    }
    // 处理文件选择
    const selectedFile = ({ target }) => {
      const file = target.files[0]
      const reader = new FileReader()
      reader.onload = evt => {
        parseOpml(evt.target.result, (err, feeds) => {
          if (currFeeds.value.length > 0) {
            // 过滤本地已有feed
            feeds = feeds.filter(feed => !currFeeds.value.find(my => my.feedUrl === feed.feedUrl))
          }
          userFeeds.value = userFeeds.value.concat(feeds)
        })
      }
      reader.readAsText(file)
    }

    const exportFile = async () => {
      if (mode.value === 1) return mode.value = 0
      const title = window.prompt('Title') || 'AkiReader'
      const header = {
        title,
        dateCreated: new Date(),
        ownerName: 'AkiReader',
      }
      const outlines = currFeeds.value.map(feed => ({
        text: feed.title,
        title: feed.title,
        xmlUrl: feed.feedUrl,
        htmlUrl: feed.url || '',
        type: feed.feedType || 'rss',
      }))
      const txt = opml(header, outlines)
      const url = URL.createObjectURL(new Blob([txt], { type: 'text/xml' }))
      const a = document.createElement('a')
      a.href = url
      a.download = 'opml'
      a.style.display = 'none'
      document.body.appendChild(a)
      a.click()
    }
    const count = ref(0)
    let timer = null
    const godMode = () => {
      if (mode.value === 1) return mode.value = 0
      if (count.value > 7) {
        count.value = 0
        const str = window.prompt()
        // eslint-disable-next-line eqeqeq
        if (str == 2 ** 10) mode.value = 1
      }
      count.value++
      clearTimeout(timer)
      timer = setTimeout(() => {
        count.value = 0
      }, 2000)
    }
    const visibleAddDialog = ref(false)
    const addObj = reactive({
      title: '',
      feedUrl: '',
    })
    const handleAdd = () => {
      if (mode.value === 1) return mode.value = 0
      userFeeds.value.push(Object.assign({}, addObj))
      visibleAddDialog.value = false
      addObj.title = ''
      addObj.feedUrl = ''
      ctx.root.$store.commit('showSnackbar', '添加成功')
    }
    const load = () => ctx.root.$store.dispatch('loadNews', ctx.root.$store.state.url || currFeeds.value[0].feedUrl)
    const handleDelete = url => {
      let flag = false
      if (currFeeds.value.findIndex(f => f.feedUrl === url) === 0) flag = true
      userFeeds.value = userFeeds.value.filter(v => v.feedUrl !== url)
      if (flag) load()
    }
    load()
    return {
      show,
      currFeeds,
      selectedFile,
      importFile,
      exportFile,
      godMode,
      showDefaultFeeds,
      visibleAddDialog,
      handleAdd,
      addObj,
      handleDelete,
      darkMode,
    }
  },
}
</script>