<template>
  <v-app :class="$store.state.dark && 'dark-mode'">
    <v-app-bar
      app
      :color="$store.state.dark ? '#424242' : 'primary'"
      :dark="$store.state.dark"
    >
      <v-app-bar-nav-icon v-if="$route.name === 'home'" @click="drawer = !drawer"></v-app-bar-nav-icon>

      <v-btn v-else text icon @click="$router.back()">
        <v-icon>mdi-arrow-left</v-icon>
      </v-btn>

      <v-toolbar-title>AkiReader</v-toolbar-title>

      <v-spacer></v-spacer>
      <v-btn v-show="$route.name === 'item'" text icon @click="linkTo($route.params.link)">
        <v-icon>mdi-open-in-new</v-icon>
      </v-btn>
      <v-btn v-show="$route.name === 'home'" text icon @click="$store.dispatch('loadNews')">
        <v-icon>mdi-refresh</v-icon>
      </v-btn>
      <v-menu v-show="$route.name === 'home'" bottom left>
        <template v-slot:activator="{ on }">
          <v-btn text icon v-on="on">
            <v-icon>mdi-router-wireless-settings</v-icon>
          </v-btn>
        </template>
        <v-list-item-group v-model="$store.state.route" color="primary">
          <v-list>
            <v-list-item
              v-for="i in 3"
              :key="i"
              @click="$store.commit('switchRoute', i - 1)"
            >
              <v-list-item-icon>
                <v-icon>mdi-numeric-{{ i }}-box</v-icon>
              </v-list-item-icon>
              <v-list-item-title>线路{{ i }}</v-list-item-title>
            </v-list-item>
          </v-list>
        </v-list-item-group>
      </v-menu>
      <v-btn
        v-show="$route.name === 'home'"
        text
        icon
        @click="$store.commit('switchTitle')"
      >
        <v-icon v-if="$store.state.onlyTitle">mdi-subtitles-outline</v-icon>
        <v-icon v-else>mdi-subtitles</v-icon>
      </v-btn>

    </v-app-bar>

    <Menu v-model="drawer" />
    <v-content :class="$store.state.dark && 'dark-bg'" v-scroll="onScroll">
      <router-view @close="drawer=false" />
    </v-content>
    <v-fab-transition>
      <v-btn
        v-show="scrollTop > 520"
        :dark="$store.state.dark"
        fixed
        fab
        bottom
        right
        :color="$store.state.dark ? '#555' : 'primary'"
        @click="$vuetify.goTo(0)"
      >
        <v-icon color="black">mdi-chevron-up</v-icon>
      </v-btn>
    </v-fab-transition>
    <v-snackbar
      v-model="$store.state.snackbar"
    >
      {{ $store.state.error }}
    </v-snackbar>
  </v-app>
</template>

<script>
import Menu from './components/Menu'
import { ref, watch } from '@vue/composition-api'

export default {
  name: 'App',

  components: {
    Menu,
  },

  setup(_, ctx) {
    const drawer = ref(false)

    const linkTo = link => window.open(link)

    const scrollTop = ref(0)

    const onScroll = e => {
      scrollTop.value = e.target.scrollingElement.scrollTop
    }

    watch(() => ctx.root.$store.state.route, () => ctx.root.$store.dispatch('loadNews'))
    return {
      drawer,
      linkTo,
      scrollTop,
      onScroll,
    }
  },
}
</script>

<style lang="sass">
body
  font-family: "Open Sans", "Helvetica Neue", Helvetica, "BBAlpha Sans", "S60 Sans", Arial, "Hiragino Sans GB", "Microsoft YaHei", "WenQuanYi Micro Hei", sans-serif

img
  max-width: 100%!important
.dark-mode
  *
    color: #000!important
.dark-bg
  background-color: #424242!important
</style>
